import React from 'react';
import { v4 as uuid4 } from 'uuid';

export default function Profile (props) {
  const {
    shopInfo
  } = props;

  let info;

  if (shopInfo !== undefined) {
    if (shopInfo.info !== undefined) {
      info = shopInfo.info.split('\n');
    }
  }

  return (
    <table className='table table-bordered'>
      <tbody>
        <tr>
          <td>
            {info !== undefined ? info.map(line => <div key={uuid4()}>{line}&nbsp;</div>) : ''}
          </td>
        </tr>
      </tbody>
    </table>
  );
}
